.tb_g_r_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100% !important;
}

.tb_g_r_post_slider{
    width: 100%;
    overflow: hidden;
}

.tb_g_r_post_wrapper{
    width: 100%;
    padding: 30px 10px;
    overflow: hidden;
}

.tb_g_r_fixed_height .tb_g_r_post_wrapper, 
.tb_g_r_fixed_height .tb_g_r_post_in{
    height: 100%;
}

.tb_g_r_post_in {
    display: flex;
    flex-direction: column;
    position: relative;
    border-radius: var(--tb-border-radius);
    overflow: hidden;
    cursor: pointer;
    background-color: #000;
    transition: box-shadow .3s ease-in, transform .3s ease-in;
}
.tb_g_r_post_media_wrapp{
    position: relative;
    display: block;
}


.tb_g_r_image_wrap_{
    position: relative;
    padding-bottom: 100%;
    background-color: rgba(0,0,0,.03);
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_g_r_image_wrap_in, .tb_g_r_video_wrap_in {
    position: absolute;
    height: 100%;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
}

.tb_g_r_image {
    width: 100% !important;
    height: 100% !important;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_g_r_image_wrap_ img{
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0 !important;
}

.tb_g_r_social_ico_ {
    font-size: 22px;
    margin: 15px 0;
    color: var(--tb-icon-color);
}


.tb_g_r_social_ {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 15px 0;
    flex-shrink: 0;
    z-index: 2;
}

.tb_g_r_social_ico_ {
    font-size: 22px;
}

.tb_g_r_author {
    width: calc(100% - 30px);
}

.tb_g_r_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    background-color: rgba(0,0,0,.2);
    overflow: hidden;
    border: 1.5px solid #fff;
}
.tb_g_r_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_g_r_authorname {
    font-size: 14px;
    font-weight:  var(--tb-author-font-weight);
    margin-bottom: 4px;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-align: left;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_g_r_authorname,
.tb_g_r_username,
.tb_g_r_time {
    font-family: var(--tb-author-font);
    color: #fff;
    font-style:  var(--tb-author-font-style);
}

.tb_g_r_seprator {
    background-color: #fff;
}

.tb_g_r_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
    z-index: 2;
}

.tb_g_r_author_wrapper {
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    padding: 10px;
    z-index: 3;
}

.tb_g_r_author_wrapper:before{
    content: '';
    height: 100px;
    width: 100%;
    background: linear-gradient(180deg, #0000 0%, rgba(0,0,0,.8) 100%);
    position: absolute;
    bottom: -2px;
    left: 0;
    z-index: 1;
}

.tb_g_r_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_g_r_post_info {
    display: flex;
    align-items: center;
}

.tb_g_r_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_g_r_username, .tb_g_r_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_g_r_username{
    max-width: 65%;
}

.tb_g_r_time{
    max-width: 35%;
}

.tb_g_r_social__ico {
    font-size: 24px;
    color: var(--tb-icon-color);
}

.tb_g_r_image_wrap_, .tb_g_r_video_wrap_{
    padding-bottom: 170%;
    position: relative;
    background-color: rgba(0,0,0,.03);
}
.tb_g_r_image, .tb_g_r_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    background-color: #000;
}

.tb_g_r_video video{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}
.tb_g_r_video video::-webkit-media-controls-panel {
    background-image: linear-gradient(transparent, transparent) !important;
}

.tb_g_r_post_in:hover .tb__media_ico_ .tb__icon,
.tb_g_r_post_in:hover .tb__media_ico_ .tb__icon{
    opacity: 0;
    transform: scaleX(.4) scaleY(.4) translateZ(0);
}
.tb_g_r_post_media_wrapp{
    position: relative;
}

.tb_g_r_like.tb_like_ico{
    bottom: 75px;
    right: 6px;
    background-color: transparent;
    box-shadow: none;
}

.tb_g_r_like.tb_g_r_like_bottom.tb_like_ico{
    bottom: 14px;
}
.tb_g_r_like.tb_like_ico .tb__icon{
    color: #fff;
    font-size: 25px;
}

.tb_g_r_like.tb_animate .tb__icon{
    color: red;
}